.homePage {
    display: grid;
        max-width: calc(100vw - 48px);
        grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
        grid-gap: 40px;
        margin-top: 1fr;
        margin-bottom: 1fr;
        padding: 0;
        list-style: none;
        margin-left: auto;
        margin-right: auto;
}